@import url(https://fonts.googleapis.com/css2?family=Mitr:wght@200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  /* font-family: 'Thai Sans Neue'; */
  font-family: "Mitr", sans-serif;
}

:root{
  --primary:#fff;
  --bg: #B62025;
  --red: #B62025;
  --yellow: #F8C915;
  --white: #fff;
  --almostBlack: #242424;
  --orange: #FB6D00;
  --lightGray:#F7F7F7;
  --darkGray:#666665;
}


.btn {
    padding: 8px 20px;
    border-radius: 4x;
    outline: none;
    border: none;
    white-space: nowrap;
    cursor: pointer ;
}

.btn--primary{
    background-color: var(--primary);
    color: var(--bg);
    border:  1px solid var(--primary);
}

.btn--outline{
    background-color: transparent !important;
    color: #fff;
    pad: 8px 20px;
    border:  1px solid var(--primary) !important;
    transition: all 0.3s ease-out;
}

.btn--outline:hover{
    background-color: #fff !important;
    color: var(--primary);
}

.btn--solid{
    background-color: var(--yellow) !important;
    color: #fff;
    cursor: pointer ;
    pad: 8px 20px;
    border:  1px solid var(--primary) !important;
    transition: all 0.3s ease-out;
}

.btn--solid:hover{
    background-color: var(--yellow) !important;
    color: var(--white);
}

.btn--large{
    padding: 12px 26px;
    font-size: 20px;
}

.btn--mobile{
    text-align: center;
    border-radius: 30px;
    width: 80%;
    text-decoration: none;
    font-size: 24px;
    background-color: transparent;
    color: #fff;
    padding: 14px 24px ;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
}    

.btn--wide{
    padding: 12px 64px;
    font-size: 20px;
}

.btn--large:hover, .btn--medium:hover,.btn--outline:hover, .btn--mobile:hover{
    transition: all 0.3 ease-out;
    background:  #fff;
    color: var(--bg);
}

.btn--wide:hover {
    color: #fff;
    background-color: #f00946;
    transition: all 0.3 ease-out;
}

.blue{
    background-color: #276afb;
    color: #fff;
    border: none;
}

.red{
    background-color: #f00946;
    color: #fff;
    border: none;
}

.primary{
    background-color: var(--bg);
    color: #fff;
    border: none;
}

.primary:hover{
    background-color: #fff;
    color:var(--bg);
    border: none;
}

.green{
    background-color: #25ce4a;
    color: #fff;
    border: none;
}

.green:hover{
    background-color: var(--bg);
    color: #fff;
    border: none;
}


.btn--link{
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 8px 12px ;
    height: 100%;
    width: 100%;
}

.navbar {
  background: var(--bg);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 80px;
  border-bottom: 5px solid rgba(0,0,0,0.2);
}

.container {
  z-index: 1;
  width: 100%;
  max-width: 1300px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 50px;
  padding-left: 50px;
}

.navbar-logo {
  color: var(--white);
  justify-self: flex-start;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.navbar-icon {
  margin-right: 0.5rem;
}

.navbar-logo-icon {
  height: 40px;
  margin: 0px 20px;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  text-align: center;
  justify-content: flex-end;
}

.nav-item {
  height: 80px;
  border-bottom: 2px solid transparent;
}

.nav-item:hover {
  border-bottom: 2px solid var(--yellow);
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.fa-bars {
  color: var(--white);
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: var(--bg);
    left: 0;
    opacity: 1;
    transition: all 0.6s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    color: var(--yellow);
    transform: scale(1.2);
    transition: all 0.3s ease;
  }

  .nav-item:hover {
    border: none;
  }

  .nav-item {
    width: 100%;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: var(--white);
    font-size: 2rem;
  }

  .nav-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 120px;
  }
}

.header__container {
  display: flex;
  margin: 50px -15px ;
  flex-wrap: wrap;
  align-content: stretch;
  align-items: center;
  font-size: 24px;
}

.header__row {
  padding-right: 15px;
  padding-left: 15px;
  flex: 1 1;
  width: 100%;
  flex-basis: 100%;
  padding:10px;
}

.header__text-center {
    color:black;
    text-align: center;
}

.header__text-underline{
    text-decoration: underline;
}

.header__text-bolder-red{
    color: var(--bg);
    font-weight: 600;
    font-size: 32px;
}

.home__hero-section {
  color: #fff;
  padding: 75px 0 100px 0;
  margin-top: 25px;
}

.home__hero-row {
  align-items: center;
}

.row {
  display: flex;
  margin-right: -15px;
  margin-bottom: -15px;
  margin-left: -15px;
  flex-wrap: wrap;
  align-content: stretch;
}

.col {
  margin-bottom: 15px;
  padding-right: 15px;
  padding-left: 15px;
  flex: 1 1;
  max-width: 50%;
  flex-basis: 50%;
}

.home__hero-text-wrapper {
  max-width: 540px;
  padding-top: 0;
  padding-bottom: 60px;
}

.top-line {
  color: #f00946;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  margin-bottom: 16px;
}

.heading {
  margin-bottom: 24px;
  font-size: 48px;
  line-height: 1.1;
  font-weight: 600;
  color: #f7f8fa;
}

h2.heading {
  font-size: 40px;
}

.subHeading {
  padding-left: 20px;
  font-size: 28px;
}

.dark {
  color: var(--bg);
}

.darkBg {
  background-color: var(--bg);
  border-radius: 100px 0 50px 0;
}

.home__hero-subtitle {
  max-width: 440px;
  margin-bottom: 35px;
  font-size: 22px;
}

.home__hero-subtitle.dark {
  color: var(--almostBlack);
}

.home__hero-img-wrapper {
  max-width: 555px;
}

.home__hero-content-simple-img-wrapper {
  width: 100%;
}

.home__hero-content-simple-img {
  border-radius: 0 150px 0 150px;
}

.home__hero-img {
  width: 95%;
  margin-top: 0;
  margin-right: 0;
  margin-left: 10px;
  padding-right: 0;
}

.home__hero-caption-wrapper {
  max-width: 555px;
  width: 95%;
  background-color: var(--primary);
  border-radius: 20px 20px 20px 20px;
  padding: 15px 25px;
  margin: 40px 0 10px 10px;
}

.home__hero-caption-wrapper h3 {
  color: var(--almostBlack);
  font-size: 26px;
  font-weight: 300;
}

.home__hero-caption-wrapper h3:nth-child(2) {
  font-weight: 400;
  text-align: center;
  margin-top: -10px;
}

.home__hero-register-wrapper {
  width: 500px;
  max-width: 100%;
  background-color: #c3caff;
  margin: auto;
  padding: 20px 10px;
  border: 10px solid white;
  box-shadow: 0px 20px 20px #888888;
  border-radius: 0px 40px 40px 40px;
}

.home__hero-register-label {
  font-size: 26px;
  color: black;
  font-weight: 400;
  min-width: 100%;
}

.home__hero-register-email {
  margin: 20px 0;
  min-width: 100%;
  display: block;
  height: calc(1.5em + 0.75rem + 10px);
  padding: 25px 10px;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.home__hero-register-button {
  width: 70%;
  margin-left: 15%;
  margin-right: 15%;
  background-color: var(--yellow);
  border: 10px solid white;
  color: white;
  border-radius: 30px;
  padding: 5px 20px;
  font-size: 26px;
  margin-top: 10px;
}

.home__hero-register-button:hover {
  background-color: #d9ac00;
}

img {
  border: 0;
  max-width: 100%;
  vertical-align: middle;
  display: inline-block;
}

@media screen and (max-width: 991px) {
  .container {
    padding-right: 0px;
    padding-left: 0px;
  }
}

@media screen and (max-width: 768px) {
  .heading {
    font-size: 36px;
  }

  h2.heading {
    font-size: 30px;
  }

  .subHeading::before {
    width: 100%;
    content: "\A";
    white-space: pre;
    text-align: right;
  }

  .home__hero-content-simple-img {
    border-radius: 0 20px 0 20px;
  }

  .home__hero-text-wrapper {
    padding-bottom: 25px;
    padding-right: 30px;
    padding-left: 30px;
  }

  .home__hero-caption-wrapper h3 {
    border-radius: 20px 20px 0 0;
  }
  .home__hero-caption-wrapper h3 {
    color: var(--almostBlack);
    font-size: 20px;
    font-weight: 400;
  }

  .home__hero-caption-wrapper h3:nth-child(2) {
    font-size: 20px;
    font-weight: 400;
    text-align: center;
    margin-top: 5px;
  }

  .home__hero-register-wrapper {
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
  }

  .home__hero-register-button {
    width: 100%;
    margin: 0;
  }

  .hide-on-desktop {
    display: none;
  }

  .col {
    max-width: 100%;
    flex-basis: 100%;
  }
}

@media screen and (min-width: 768px) {
  .home__hero-row:nth-child(1) {
    align-items: flex-start;
  }

  .hide-on-mobile {
    display: none;
  }

  .home__hero-text-wrapper {
    padding-top: 40px;
  }

  .home__hero-img-wrapper {
    transform: scale(1);
    transition: all 0.3s ease-out;
    color: #1c2237;
  }

  .home__hero-img-wrapper:hover {
    transform: scale(1.1);
    transition: all 0.3s ease-out;
    color: #1c2237;
  }
}

.faq__wrapper {
  display: flex;
  flex-direction: column;
  border-radius: 10px ;
}

.faq__question-wrapper {
  background-color: var(--lightGray);
  border-radius:  10px;
  font-size: 32px;
  border: 1px solid var(--darkGray);
  cursor: pointer;
  padding: 15px 15px 0px 15px;
}

.faq__question-wrapper h3 {
  font-weight: 400;
}

.faq__answer-wrapper {
  background-color: var(--primary);
  margin: 15px;
  font-size: 24px;
  max-height: 1500px;
  transition: all 1s ease-in-out;
}

.faq__answer-wrapper.hide {
  max-height: 0px;
  opacity: 0;
  visibility: hidden;
  transition: max-height 0.5s ease-in-out, opacity 0.6s ease-in-out,
    visibility 0.6s ease-in-out, all 0.6s ease-in-out;
}

@media screen and (max-width: 960px) {
  .faq__question-wrapper h3 {
    font-size: 24px;
  }
  .faq__answer-wrapper {
    font-size: 20px;
  }
}

.pricing__section {
    padding: 50px 0 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: var(--yellow);
  }
  
  .pricing__container-card {
    background: var(--primary);
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.2);
    width: 200px;
    text-decoration: none;
    border-radius: 10px;
  }
  
  
  .pricing__container-card:hover {
    transform: scale(1.06);
    transition: all 0.3s ease-out;
    color: #1c2237;
  }
  
  .pricing__container-card:nth-child(2) {
    margin: 0 24px;
  }
  
  .pricing__container-card:nth-child(4) {
    margin: 0 24px;
  }
  
  .pricing__container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .pricing__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 auto;
  }
  
  .pricing__heading {
    color: #1c2237;
    margin-bottom: 24px;
  }
  
  .pricing__container-cardInfo {
    display: flex;
    flex-direction: column;
    padding: 24px;
    align-items: center;
    color: black;
  }
  
  .pricing__container-cardInfo h3 {
    margin-bottom: 5px;
    font-size: 24px;
  }
  
  .pricing__container-cardInfo h4 {
    font-size: 40px;
  }
  
  .pricing__container-cardInfo p {
    font-size: 14px;
    margin-bottom: 24px;
    color: var(--red);
  }

  .cross-out{
    text-decoration: line-through;
  }
  
  .pricing__container-features {
    margin: 16px 0 32px;
    list-style: none;
  }
  
  .pricing__container-features li {
    margin-bottom: 10px;
  }
  
  .pricing__container-features {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .icon {
    margin: 24px 0;
  }
  
  @media screen and (max-width: 960px) {
    .pricing__container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
    }
  
    .pricing__container-card {
      width: 90%;
      margin:24px;
    }
  
    .pricing__wrapper {
      margin: 0 30px;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  
    .pricing__container-card:hover {
      transform: none;
    }
  }
.footer-container {
  background-color: var(--orange);
  padding: 4rem 0 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer__main-wrapper{
  display: flex;
  flex-direction: column;
}

.footer__text-wapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.footer__text {
  padding: 10px 20px;
  font-weight: 400;
  font-size: 26px;
  color: white;
  text-decoration: none;
}


.footer__text:hover {
  text-decoration: underline;
}

.social-logo {
  color: #fff;
  justify-self: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.facebook:hover .social-logo{
  color: #036BE4 !important;
}

.line:hover .social-logo{
  color: #00B900 !important;
}

.phone:hover .social-logo{
  color: #24ADF3 !important;
}

.email:hover .social-logo{
  color: #EA4335 !important;
}

@media screen and (max-width: 768px) {
  .footer__text{
    font-size: 20px;
  }
}
